<script>
import { mapActions, mapState, mapGetters } from "vuex";
import Footer from "./Footer";
import HeaderTicket from "./HeaderTicket";
import Modal from "./Modal";
import Table from "./Table";
import moment from "moment";

import { TheMask } from 'vue-the-mask'

export default {
  name: "FormTicket",

  components: {
    Table,
    HeaderTicket,
    Footer,
    Modal,
    TheMask
  },
  data() {
    return {
      //
      arrTripInfo: [],
      client: {},
      //BUS SEATS
      seats: {},
      arr: {},
      free: 0,
      //    icons
      keys: [],
      //bookes
      //    count

      // price: 200,
      array: [],

      loading: false,
      //    redirect_link
      redirect_link: "",
      filterArraySeats: [], //array seats numbers

      //totalPrice
      totalPrice: 0,

      isOpenModal: false,
    };
  },
  computed: {
    ...mapState({
      objbuy: (state) => state.objbuy,
      token: (state) => state.token,
      isAuth: (state) => state.isAuth,
      ticketsbuy: (state) => state.ticketsbuy,
    }),
    ticket() {
      return this.$store.getters.ticket(this.$route.params.id);
    },
    calcTotalPrice() {
      // console.log(totalPrice);
      return this.ticket.counterBuy * this.price;
    },
  },
    // updated() {
      // console.log(this.loading + " updated");
  // console.log(this.totalPrice());
  // this.totalPrice()
  // console.log(this.totalPrice + " updated");
    // },
  //   beforeMount() {
  //     console.log(this.loading + " beforeMount");
  // console.log(this.totalPrice());
  //   },
  // watch: {
    // totalPrice() {
      //   console.log(totalPrice);
      // this.totalPrice = this.ticket.counterBuy * this.price;
      //   return this.ticket.counterBuy * this.price;
    // },
  // },
  created() {
    this.loading = true;
    // console.log(this.loading);
    // console.log(this.totalPrice());

    // get LEFT ? RIGHT (LEFT info) and right PLACES

    axios
      .post("web/ticket/checkout", {
        trip_id: this.objbuy.trip_id,
        from_id: this.objbuy.from_id,
        to_id: this.objbuy.to_id,
        date: this.objbuy.date,
      })
      .then((res) => {
        this.arrTripInfo = { ...res.data.result };
        this.seats = res.data.result.schema;
        this.price = res.data.result.trip.price;
        this.free = res.data.result.free_seat;
        console.log(this.arrTripInfo.id);
        console.log("PRICE 1 TICKET ", this.price);
        console.log("Місця автобуса ", this.seats);
        this.loading = false;
        // console.log(this.loading + " created");
      });

    //GET CLIENT INFORMATION
    const headers = {
      "Content-type": "application/json",
      Authorization: `Bearer ${this.token}`, //передача токена
    };
    axios
      .get("web/user", {
        headers: headers,
      })
      .then((response) => {
        this.client = response.data.result;
      })
      .catch((e) => {
        if (e.response.status === 401) {
          console.log("Не авторизований");
        }
      });
  },
  methods: {
    ...mapActions(["AUTH_LOGOUT"]),
    LOGOUT() {
      this.$store.dispatch("AUTH_LOGOUT").then(() => {
        this.$router.push("/");
      });
    },
    handleClick(index1, index2) {
      const seats = JSON.parse(JSON.stringify(this.seats));
      const item = seats[index1][index2];
      if (item.status === "free" && item.type === "seat" && item.num !== 0) {
        if (item.isBooked) {
          item.isBooked = !item.isBooked;
          this.array = this.array.filter((el) => el.num !== item.num);
        } else {
          item.isBooked = !item.isBooked;
          this.array.push(item);
        }
        // console.log("handleClick " + this.ticket.counterBuy);
        this.ticket.counterBuy = this.array.length;
        console.log("handleClick " + this.ticket.counterBuy);
        console.log("BUY SEATS ", this.array, "his length ", this.array.length);

        // масив з номерами місць
        this.filterArraySeats = this.array.map(({ num }) => num);
        console.log("Numbers seats ", this.filterArraySeats);
      }
      this.seats = seats;
    },
    addCounter() {
      const tickets = this.ticketsbuy.slice();
      const index = this.ticketsbuy.findIndex(
        (item) => item.id === this.ticket.id
      );
      tickets[index] = {
        ...tickets[index],
        counterBuy:
          tickets[index].counterBuy < this.ticket.detail.route.free_seat
            ? tickets[index].counterBuy + 1
            : tickets[index].counterBuy,
      };

      //   console.log("addCounter tickets " + tickets);
      //   console.log("addCounter index " + index);
      // console.log("addCounter " + this.ticket.counterBuy);
      // this.showTotalPrice();
      this.$store.commit("haveTickets", tickets);
    },
    incCounter() {
      const tickets = this.ticketsbuy.slice();
      const index = this.ticketsbuy.findIndex(
        (item) => item.id === this.ticket.id
      );
      tickets[index] = {
        ...tickets[index],
        counterBuy:
        tickets[index].counterBuy > 1
          // tickets[index].counterBuy > 0
            ? tickets[index].counterBuy - 1
            : tickets[index].counterBuy,
      };
      // console.log("incCounter" + this.ticket.counterBuy);
      this.$store.commit("haveTickets", tickets);
    },
    // ROUTE BUY TICKET
    doPayment() {
      axios
        .post("web/ticket/buy", {
          from_id: this.objbuy.from_id,
          to_id: this.objbuy.to_id,
          seats: this.filterArraySeats,
          date: this.objbuy.date,
            trip_id:this.arrTripInfo.id,
          user: {
            first_name: this.client.first_name,
            last_name: this.client.last_name,
            email: this.client.email,
            phone: this.client.phone,
          },
        })
        .then((res) => {
          this.redirect_link = res.data.result.redirect_link;
          window.location.href = this.redirect_link;
        })
        .catch((e) => {
          // if (e.response.status === 401) {
          console.log(e);
          // }
        });
      // this.$router.push({name: 'Tickets'})
    },
    showTotalPrice(){
      this.totalPrice = this.ticket.counterBuy * this.price;
    },
    openModal() {
      this.isOpenModal = true;
    },
    closeModal() {
      this.isOpenModal = false;
    },
  },
};
</script>

