<template>
    <div>
        <HeaderTicket :name="client.first_name" :handle-open="openModal" />
        <Modal :is-open-modal="isOpenModal" :close-modal="closeModal" />
        
        <b-container class="text-left pl-3 mx-auto breadcrumbs-wrapper" >
            <a href="#" class="nav-menu-link decoration-under">Головна</a> /
            <!--            <span class="nav-menu-link"> /Купити квиток/</span>-->
            <a href="#" class="nav-menu-link" style="color: #499625;">Оформлення квитка</a>
        </b-container>
        <b-container >
            <div class="text-left pt-4 pb-2 title-ticket">Квитки на автобус</div>
        </b-container>


        <b-container >
            <div class="d-flex text-left text-black row-table pb-2 pl-0 ticket-bus__list" >
                <b-col class="title-table-col">Звідки</b-col>
                <b-col class="title-table-col">Куди</b-col>
                <b-col class="title-table-col">К-сть пасажирів</b-col>
                <b-col class="title-table-col">Дата поїздки</b-col>
            </div>

            <RowFind/>

        </b-container>

        <div class="yellow-container d-flex align-items-center">
            <div class="d-flex justify-content-between pl-4 pr-4 align-items-center flex-wrap yellow-wrapper">
                <div class="text-left pr-5">
                    <div class="title-bur">
                        <div>BUR-BUR завжди з тобою!</div>
                        <div class="title-bottom-trip">Розклад рейсів і ваші квитки завжди будуть під рукою.</div>
                    </div>
                </div>
                <div class="d-flex flex-wrap">
                    <div class="pr-5">
                        <img src="@/assets/images/mobile.png">
                    </div>
                    <div class="d-flex align-items-center flex-wrap">
                        <div><img src="@/assets/images/qr-code.jpg" alt="" class="pr-2 qr"></div>
                        <div><img src="@/assets/images/icons/apple-store.svg" class="pr-2"></div>
                        <div><img src="@/assets/images/icons/google-play.svg" class="pr-2"></div>
                    </div>
                </div>
            </div>
        </div>

        <Footer/>
    </div>
</template>

<script>
    import HeaderTicket from "./HeaderTicket";
    import Footer from "./Footer";
    import RowFind from "./RowFind";
    import Modal from "./Modal";
    import {mapState} from 'vuex'

    export default {
        name: "TicketBus",
        data() {
            return {
                client: {},
                isOpenModal: false,
            }
        },
        computed: mapState([
            'token',
            'isAuth'
            //получаем токен (ключ токена)  из state
        ]),
        components: {
            RowFind,
            HeaderTicket, 
            Footer,
            Modal
        },
        created() {
            const headers = {
                'Content-type': 'application/json',
                'Authorization': `Bearer ${this.token}`, //передача токена
            }
            axios.get('web/user', {
                headers: headers
            })
                .then((response) => {
                    this.client = response.data.result
                    console.log("Client ",this.client);
                })
                .catch(e => {
                    if (e.response.status === 401) {
                        console.log('Не авторизований')
                    }
                });
        },
        methods: {
            openModal() {
                this.isOpenModal = true;
            },
            closeModal() {
                this.isOpenModal = false;
            },
        }
    }
</script>

