            v-model="clientInput.phone"
            v-mask="'### ## ###-##-##'"
          /> -->
          <the-mask class="inp-tel-value input-tel label-bg" @input="onlyNumbers" mask="+38 (0##) ### ## ##" v-model="clientInput.phone" type="tel"  :class="{'notvalid': failedValidation.userNumber, 'valid': failedValidation.userNumber===false}"  placeholder="+38 (0XX) XXX XX XX"></the-mask>
        </b-col>
        <b-col class="mb-3 p-0">
          <p>Пароль</p>
          <div class="d-flex ">
            <input
              :type="type"
