          <input
            type="text"
            class="label-bg"
            placeholder="Введіть прізвище"
            v-model="clientInput.last_name"
        /></b-col>

        <b-col class="mb-3 p-0">
          <p>Email</p>
          <!-- <input
            type="telephone"
            v-model="clientInput.email"
            class="label-bg"
            placeholder="Введіть email" -->
            <input
            type="email"
            v-model="clientInput.email"
            class="label-bg"
            placeholder="Введіть email"
        /></b-col>

        <b-col class="mb-3 p-0">
          <p>Фото</p>
          <input type="file" name="file" id="file" class="inputfile" />
          <label for="file" class="title-holder-change">Змінити фото</label>
          <img
            src="@/assets/images/icons/photo-icon.svg"
            alt=""
            class="photo-icon"
          />
        </b-col>
      </div>
      <button class="btn-save" @click="updateUser()">Зберегти зміни</button>
    </b-container>

    <Footer />
  </div>
</template>

<script>
import { mapState } from "vuex";
import HeaderTicket from "./HeaderTicket";
import Footer from "./Footer";
import { TheMask } from 'vue-the-mask'

export default {
  name: "Profile",
  components: { HeaderTicket, Footer, TheMask },
  data() {
    return {
      client: {}, //Old User
      clientInput: {}, // Updated User
      name: "",
      phone: "",
      //    load img
      selectedFile: null,

      //pass
      type: "password",
      btnText: "Show Password",

      //v-mask
      // mask: "## ## ###-##-##",
      failedValidation: {
        userNumber: null
      }
    };
  },
  computed: mapState([
    "token",
    "isAuth",
    //получаем токен (ключ токена)  из state
  ]),
  created() {
    const headers = {
      "Content-type": "application/json",
      Authorization: `Bearer ${this.token}`, //передача токена
    };
    axios
      .get("/mobile/user", {
        headers: headers,
      })
      .then((response) => {
        this.client = response.data.result;
        console.log("CLEINT data: ", this.client);
        this.clientInput = { ...response.data.result };
      })
      .catch((e) => {
        if (e.response.status === 401) {
          // alert("Авторизуйся ", e);
          localStorage.removeItem("retriewToken");
          this.$router.push({ name: "Home" });
        }
      });
  },
  methods: {
    updateUser() {
      const headers = {
        "Content-type": "application/json",
        Authorization: `Bearer ${this.token}`, //передача токена
      };
      axios
        .put(
          "mobile/user/" + this.client.id,
          {
            ...this.clientInput,
          },
          {
            headers: headers,
          }
        )
        .then((res) => {
          this.client = res.data.result;
        })
        .catch((e) => {
          if (e.status === 401) {
            console.log("User did not update");
          }
          return Promise.reject(e.json());
        });
    },
    openModal() {
      this.isOpenModal = true;
    },
    closeModal() {
      this.isOpenModal = false;
    },
    //pass
    showPassword() {
      this.type === "password"
        ? (this.type = "text")
        : (this.type = "password");
    },
    onlyNumbers() {
            // this.failedValidation.userNumber = !/^\+[\d]{12}$/.test(this.userNumber) ? true : false;
            // this.failedValidation.userNumber = !/^[\d]{12}$/.test(this.userNumber) ? true : false;
       
            this.failedValidation.userNumber = (this.userNumber).length < 9 ? true : false;
       },
  },
};
</script>
